import Main from "./Main.vue";
import store from "../state/store.js";


Vue.filter("shortLink", function(url) {
    const colors = url.split("/");
    return colors.length > 0 ? colors[colors.length - 1] : "Link"
});

Vue.filter("money", function(value) {
    if (!value) {
        return "0.00"
    }
    if (typeof value !== "number") {
        return value;
    }
    var formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'KES',
        minimumFractionDigits: 0
    });
    return formatter.format(value);

});

frappe.provide("frappe.supplier"); // create a namespace within the Frappe instance

frappe.supplier.Chart = class {
    // create a glue class, wich will manage your Vue instance
    constructor({ parent }) {
        this.$parent = $(parent);
        this.page = parent.page;
        this.make_body();
    }

    make_body() {
        $('.sticky-top').addClass("hidden");
        this.$body = this.$parent.find(".layout-main-section");
        this.$parent.find(".page-head").addClass("hidden");
        this.$parent.find(".page-body").removeClass("container");
        this.$parent.find(".content").removeClass("page-container");
        this.$parent.find(".page-content").removeClass("page-content");
        this.$parent
            .find("layout-main-section-wrapper")
            .addClass("no-bottom-margin");
        this.$page_container = $('<div class="hub-page-container">').appendTo(
            this.$body
        );

        new Vue({
            store,
            router: frappe.custom.router,
            el: ".hub-page-container",
            render: (h) => h(Main),
        });
    }
};