export const api = ({ method, args = {}, freeze=true  }) =>
    new Promise((resolve, reject) =>
        frappe.call({
            method,
            freeze,
            args,
            callback: ({ message }) => resolve(message),
            error: reject,
        })
    );

export const getSupplierRfqs = (payload) =>
    api({
        method: "mtrh_dev.mtrh_dev.get_supplier_rfqs",
        args: payload,
    });

export const getRfqItems = (payload) =>
    api({
        method: "mtrh_dev.mtrh_dev.get_supplier_rfq_items",
        args: { rfq: payload },
    });

export const postRfqItems = (payload) =>
    api({
        method: "mtrh_dev.mtrh_dev.create_supplier_quotation",
        args: { doc: payload },
    });
export const getSupplier = (username) =>
    api({
        method: "mtrh_dev.mtrh_dev.get_linked_supplier",
        args: { username: username },
    });